//    

import React, { useEffect,} from "react";

import "../css/common.css";

//  JS 
import { wheelFn,initSet, zeroPno, evtFn, movePg } from "../func/smoothScroll23";

//  
import { Banner } from "../modules/Banner";
import { Artists } from "../modules/Artists";
import { Intro } from "../modules/Intro";
import { Album } from "../modules/Album";
import { Audition } from "../modules/Audition";

// 
import $ from 'jquery';

export function MainCont(){
//     !
 useEffect(() => {

  //  
  $('html,body').css({overflow:'hidden'});

  //  
  window.addEventListener('wheel',wheelFn);

  // +    ///
  evtFn();

  // +    ///
  movePg();

  //   
  initSet();

  //   
  zeroPno();

  



  //  
  return(()=>{
    console.log(' ~!');

    //      !
    window.removeEventListener('wheel',wheelFn);
    $('html,body').css({overflow:'visible'});

    //       
    //     off()
    $('.gnb li').off('click');
    $(document).off('keydown');

  }); ////////////  return //////////

 }, []); ////////// useEffect ////////////


return (
    <main id="main-area" className="site-body">
        {/* 2-1.   */}
        <section id="movie-area" className="main">
        <Banner />
        </section>
        {/* 2-2.   */}
        <section id="intro-area" className="main">
        <Intro />
        </section>
        {/* 2-3.   */}
        <section id="ats-area" className="main">
        <Artists />
        </section>
        {/* 2-4.   */}
        <section id="album-area" className="main">
        <Album />
        </section>
        {/* 2-5.   */}
        <section id="aud-area" className="main">
        <Audition />
        </section>

        {/*    */}
        <nav className="indic">
            <ul>
                <li className="on">
                    <a href="Banner">
                        <span></span>
                    </a>
                </li>
                <li>
                    <a href="Intro">
                        <span></span>
                    </a>
                </li>
                <li>
                    <a href="Artists">
                        <span></span>
                    </a>
                </li>
                <li>
                    <a href="Album">
                        <span></span>
                    </a>
                </li>
                <li>
                    <a href="Audition">
                        <span></span>
                    </a>
                </li>
            </ul>
        </nav>
    </main>
);
} ///////// MainCont  /////////////

export default MainCont;