// theblack PJ    
// GNB 
import { useNavigate } from "react-router-dom";
import { GnbHam } from "../modules/HamGnb";

//  CSS 
import '../css/common.css'



//  API
import { bCon } from "../modules/TbContext";
import { useCallback } from "react";
import { Language } from "../modules/Language";


/******************************************************* 
  [       ]
  1. <Link to="/"></Link>
  -> to  <Route path="/">  !

  2. <Outlet />
  ->     
  ->  MainArea  !
*******************************************************/

//  ! /////
// -> .....    !
// ?  API  /  
//       
//    !!!
// ->>> ? API  
// props       !
// -> React.memo   
//    !
// ->    useCallback()   !!!



export function TopArea(){

  //  
  const goNav = useNavigate();
  
  //   : pgName -  / param - 
  const chgPage = useCallback((pgName,param) => goNav(pgName,param),[]);
  //   TopArea   
  // useCallback     
  //     !
  // useCallback(,[])
  // ->      !(, !)

  //  ///////////////////////////

  return(
    <bCon.Provider value={{ chgPage }}>
    {/* 1.   */}
    <header id="top-area" className="site-header">
            {/* 1-1.   */}
            <div className="langicon">
                <img src="./images/global.png" alt=""/>
            </div>
            {/* 1-2.   */}
            <div className="TBlogo">
                <a href="#">
                <img src="./images/.png" alt=" "/>
                </a>
            </div>
            {/* 1-3.   */}
            <div className="TBham">
                <span></span> <span></span> <span></span>
            </div>
            {/* GNB  */}
            <GnbHam chgPageFn={chgPage} />
            {/*   */}
            <Language />
    </header>
    </bCon.Provider>
  )
} /////////////// TopArea  ///////////////


