// TheBlack   

// 
import { mvData } from "../data/mv_data";

import "../css/banner.css";

import { useEffect } from "react";

//  + UI
import $ from "jquery";
import "jquery-ui-dist/jquery-ui";

//  
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faChevronLeft, faChevronRight } from "@fortawesome/free-solid-svg-icons";


//  

import '../func/i18n';
import i18next from "../func/i18n"
import { useTranslation } from "react-i18next";

//   //
export function Banner(){
  const { t } = useTranslation();

    // 1. 
    // (0) 
    const A_TM = 300;
    // (1) 
    const A_ES = "easeInOutQuint";
    // (2) (1-,0-)
    let cSts = 0;
    // (3) 
    let sNum = 0;

    const goSlide = (e) => {
      const tg = e.target;
      // console.log(tg);

    // 2. 
    // (1) 
    const sldBox = $(tg).parent().siblings(".ats-movie-area");
    // console.log(":",sldBox);
    // (2)  
    const bindic = $(tg).parent().siblings('.bindic').find('li');
    // console.log(':',bindic);
    // (3)  
    const sCnt = sldBox.find('li').length;
    // console.log(':', sCnt);

    // 3.    
    // 3-1.  

      // 0.  //////////
      if(cSts) return;
      cSts=1; // 
      setTimeout(()=>cSts=0,A_TM);
      ///////////////////////////

      // 1.  
      let isR = $(tg).parent().is('.rar');
      // console.log('!', isR);

      // 2. 
      // 2-1. 
      if(isR){
          //   
          // left -100%
          sldBox.animate({ left: "-100%" }, A_TM, A_ES,
          () => { // ()
              // li  
              sldBox.append(sldBox.find('li').first())
              //  left 0 !
              .css({left:'0'});
          });
          //   (  0)
          sNum++;
          if(sNum>=sCnt) sNum=0;
      } ////////// if ///////////

      // 2-2. 
      else{
          // li  
          sldBox.prepend(sldBox.find('li').last())
          // left -100%
          .css({left:'-100%'})
          // left 0 
          .animate({left:'0'},A_TM, A_ES);

          //  (0  )
          sNum--;
          if(sNum<0) sNum = sCnt-1;
      } ////////// else ///////////

      // console.log(' :',sNum);

      //  'on'(li )
      bindic.eq(sNum).addClass('on')
      .siblings().removeClass('on');

    } ////////// goSlide ////////////

      // 
      const selData = mvData;

      //   ///////////
      const makeList = (data) => {
        // console.log(data);
        return data.map((v, i) => (
          <li key={i}>
            {/*  */}
            <video src={v.vsrc} alt="" 
            id="mv"
            loop="loop"
            muted="muted"
            autoPlay="autoplay"
            />
            {/*   */}
            <div className="mtit">
              <h2>{t(v.vtit)}</h2>
            </div>
          </li>
        ));
      }; ////////// makeList  //////

      ///////////////////////////////////
      //  ///////////////////////
      return(

        <section className="ats-movie">
          {/*  */}
          <ul className="ats-movie-area">{makeList(selData)}</ul>
          {
            selData.length > 1 && (
              <>
              {/*    */}
              {/*   */}
                <div className="arrow lar"
                onClick={goSlide}>
                  <img src="./images/white-arrow.png" alt="" />
                </div>
                <div className="arrow rar"
                onClick={goSlide}>
                  <img src="./images/white-arrow.png" alt="" />
                </div>
                <ol className="bindic">
                  {selData.map((v, i) => (
                <li className={i == 0 ? "on" : ""} key={i}></li>))}
                  </ol>
              </>
              )
              }
      </section>
      ); ///////// return //////////
} /////////// Banner  //////////