// TAEYANG    

//    

import React, { useRef, useState } from 'react';

// Import Swiper React components
import { Swiper, SwiperSlide } from "swiper/react";

// Import Swiper styles
import "swiper/css";
import "swiper/css/navigation"

import { Navigation } from 'swiper/modules';

//   CSS 
import "../plugin/css/swiper_ats.css"

//   
import { detailData } from "../data/detail_data.js";

//   css 
import "../plugin/css/swiper_alb.css"

//  

import '../func/i18n';
import i18next from "../func/i18n"
import { useTranslation } from "react-i18next";

//  + UI
import $ from "jquery";
import "jquery-ui-dist/jquery-ui";

export function TAEYANG(){
  const { t } = useTranslation();

  const selData = detailData;

  const makeList = (data) => {
    // console.log(data);
    return data.map((v, i) => (
          <div className='detail-container' key={i}> 
                <div className="detail-title">
                  <h3>{t(v.ats1)}</h3>
                </div>
                <div className="detail-profile">
                  <img src={v.isrc1} alt=" " />  
                </div>
                <div className="detail-cont">
                  <div className="detail-name">
                    <h2>{t(v.alt1)}</h2>
                  </div>
                  <div className="detail-sns1">
                    <a href={v.insta1}
                      target='_blank'
                      title=" ">
                        <img src="./images/insta_img_white-remove.png" alt=" " />
                    </a>
                    <a href={v.youtube1}
                      target='_blank'
                      title=" ">
                        <img src="./images/youtube_img_white-remove.png" alt=" " />
                    </a>
                    <a href={v.twitter1}
                      target='_blank'
                      title=" ">
                      <img src="./images/twitter_img_white-remove.png" 
                      alt=" " />
                    </a>
                  </div>
                  <div className="detail-career">
                    <h2>{t("ALBUM")}</h2>
                    <p>{t("2023 | Down to Earth")}</p>
                    <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{t("VIBE(feat.Jimin of BTS)")}</p> 
                    <p>{t("2022 | ")}</p>
                    <br />
                    <h2>{t("PERFORMANCE")}</h2>
                    <p>{t("2023 | Rapbeat Festival")}</p>
                    <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{t("SUMMER SONIC (&, )").split("^")[0]}</p> 
                    <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{t("SUMMER SONIC (&, )").split("^")[1]}</p> 
                    <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{t("Seen Festival()")}</p> 
                    <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{t("SO WONDERFUL FESTIVAL")}</p>
                    <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{t(" ")}</p>  
                    <br />
                    <h2>{t("ADVERTISING")}</h2>
                    <p>{t("2023 | ")}</p>
                    <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{t("")}</p> 
                    <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{t("")}</p> 
                  </div>
                  <div className="detail-gubun">
                    <hr />
                  </div>
                  <div className="detail-gallery">
                  <h2>{t("GALLERY")}</h2>
                  </div>
                    <Swiper
                      loop={true}
                      breakpoints={{
                        530:{
                          slidesPerView:3,
                          spaceBetween:30
                        },
                        300:{
                          slidesPerView:2,
                          spaceBetween:50
                        },
                      }}
                      navigation={true}
                      modules={[Navigation]}
                      spaceBetween={50}
                      className='detail-swiper'>
                      <SwiperSlide className='detail-slide'>
                        <img src={v.timg11} alt="1" />
                      </SwiperSlide >
                      <SwiperSlide className='detail-slide'>
                        <img src={v.timg12} alt="2" />
                      </SwiperSlide>
                      <SwiperSlide className='detail-slide'>
                        <img src={v.timg13} alt="3" />
                      </SwiperSlide>
                      <SwiperSlide className='detail-slide'>
                        <img src={v.timg14} alt="4" />
                      </SwiperSlide>
                      <SwiperSlide className='detail-slide'>
                        <img src={v.timg15} alt="5" />
                      </SwiperSlide>
                      <SwiperSlide className='detail-slide'>
                        <img src={v.timg16} alt="6" />
                      </SwiperSlide>
                    </Swiper>
                    <div className="detail-gubun2">
                      <hr />  
                    </div>
                    <div className="detail-video">
                      <h2>{t("VIDEOS")}</h2>
                    </div>
                    <div className="detail-video-cont">
                    <div className="detail-video-grid-first">
                      <a href={v.mlink9}
                        target='_blank'
                        title=" "
                        className="video-youtube">
                        <img src={v.mv9} alt=" " />
                      </a>
                        <aside>
                        <h3>{t(v.mtit9).split("^")[0]}</h3>
                        <h3>{t(v.mtit9).split("^")[1]}</h3>
                        <h3>{t(v.mtit9).split("^")[2]}</h3>
                        </aside>
                      </div>
                          <div>
                      <a href={v.mlink10}
                        target='_blank'
                        title=" "
                        className="video-youtube">
                        <img src={v.mv10} alt=" " />
                      </a>
                        <aside>
                        <h3>{t(v.mtit10)}</h3>
                        </aside>
                      </div>
                      <div>
                      <a href={v.mlink11}
                        target='_blank'
                        title=" "
                        className="video-youtube">
                        <img src={v.mv11} alt=" " />
                      </a>
                        <aside>
                        <h3>{t(v.mtit11)}</h3>
                        </aside>
                      </div>
                    </div>      
                </div>   
          </div>
    ))
    } /////////// makeList  ///////////

  //  ////////////////  
  return(
      <section id="ats-Detail-area1">
        {makeList(selData)}
      </section>
  );
} //////////// TAEYANG  //////////////

export default TAEYANG;