//     JS : jquery-autoScroll.js

//   ! /////


//  
import $ from 'jquery';
import ScrollToTop from '../modules/ScrollToTop';
window.jQuery = $;
require('jquery-ui-dist/jquery-ui');
require('jquery-ui-touch-punch/jquery.ui.touch-punch');

  /****************************************** 
     
  ******************************************/
  //  
  let pno = 0;
  //   
  const zeroPno = () => {pno=0};
  //  
  let pg;
  //  
  let pgcnt;
  // console.log(":", pgcnt, pg, pno);
  // 
  let prot = [];
  // 
  prot[0] = 0;

  //     
  $(()=>{
    //  
    pg = $(".main");
    //  
    pgcnt = pg.length;

  }); //////////////// load /////////////


  /****************************************** 
     
    ->>>   
         
       
     ! -> ?
     JS   !
    ?   
        
        
    ! 
  ******************************************/
  //   
  // $(window).on("wheel", wheelFn); ->  X

  //    (!)
  $("html,body").animate({ scrollTop: "0px" });

  /**************************************** 
    : wheelFn
    :    
    ->   
    ****************************************/
  function wheelFn(e) {
    // 
    if (prot[0]) return;
    chkCrazy(0);
    // console.log("~~~~~~!");

    // 1. 
    let delta = e.wheelDelta;
    // console.log(delta);

    // 2.    
    if (delta < 0) {
      pno++;
      if (pno === pgcnt) pno = pgcnt - 1;
      //   !
    } //// if /////
    else {
      pno--;
      if (pno === -1) pno = 0;
      //  !
    } //// else ////

    // console.log(':',pno);

    // 3.   +  "on"
    movePg();
  } /////////////// wheelFn  ///////////////

  /******************************************** 
    : chkCrazy
    :   
    ********************************************/
  function chkCrazy(seq) {
    // seq  
    prot[seq] = 1;
    setTimeout(() => (prot[seq] = 0), 800);
  } //////// chkCrazy //////////////

  /******************************************** 
    : movePg
    :  
    ********************************************/
  function movePg() {
    // : html,body ->     !
    $("html,body")
      .stop()
      .animate({
          scrollTop: 
          $(window).height() * pno + "px",
        }, 700,"easeInOutQuint",actPage
        //   actPage !
      ); ///// animate //////

      //   on 
      addOn();

  } ///////////////// movePg ////////////////

  /////////////////////////////////////////////////////
  //     'on' //
  /////////////////////////////////////////////////////
  //  +      !
  const addOn = () => {
    //   class 'on' 
    $(".indic li").eq(pno).addClass('on')
    .siblings().removeClass('on');
  }; //////////// addOn ////////////

  /*************************************** 
     : initSet
     :   
  ***************************************/
 function initSet(){
  // 1. 

  // :  - .intTit
  $('.intTit').css({
    opacity: 0,
    top:'10vh',
    transition: 'all ease-out 1s',
    display: 'inline-block'
  }); /////////// css //////////

  // :  - .intCont
    $('.intCont').css({
      opacity: 0,
      top:'33vh',
      transition: 'all ease-out 1s',
      display: 'inline-block'
    // :  - .intCont
    }); /////////// css //////////

  $('.ats-title').css({
    opacity: 0,
    top:'22vh',
    transition: 'all ease-out 1s',
    display: 'inline-block'
  }); /////////// css //////////

  $('.alb-title').css({
    opacity: 0,
    top:'22vh',
    transition: 'all ease-out 1s',
    display: 'inline-block'
  }); /////////// css //////////

  $('.aud-title').css({
    opacity: 0,
    top:'22vh',
    transition: 'all ease-out 1s',
    display: 'inline-block'
  }); /////////// css //////////


  ////////////////  (init) //////////////

 } /////////// initSet  ///////////////

  /***************************************** 
  : actPage
  :    
 *****************************************/
function actPage(){
  // console.log('~!!!', pno);

  // pno 0  5  !
    // :  .main  .intTit
    $('.main').eq(pno).find('.intTit')
    .css({
      top:'17vh',
      opacity: 1
    }); ///////// css /////////

  // pno 0  5  !
    // :  .main  .intCont
    $('.main').eq(pno).find('.intCont')
    .css({
      top:'40vh',
      opacity: 1
    }); ///////// css /////////


  // pno 0  5  !
    // :  .main  .ats-title
    $('.main').eq(pno).find('.ats-title')
    .css({
      top:'12vh',
      opacity: 1
    }); ///////// css /////////

  // pno 0  5  !
    // :  .main  .ats-title
    $('.main').eq(pno).find('.alb-title')
    .css({
      top:'12vh',
      opacity: 1
    }); ///////// css /////////

  // pno 0  5  !
    // :  .main  .ats-title
    $('.main').eq(pno).find('.aud-title')
    .css({
      top:'12vh',
      opacity: 1
    }); ///////// css /////////

  //   !
  if(pno==0) initSet();

} ///////// actPage  //////////////////

//   /////////
function evtFn(){

  /////////////////////////////////////////////
  // GNB  +     //
  /////////////////////////////////////////////
  $('.indic li').click(function(){
    // 1. 
    let idx = $(this).index();
    // console.log('~!',idx);

    // 2.   (!)
    pno = idx;

    // 3.  
    movePg();

  }); ///// click //////////

  //      !
  $('.TBlogo a').click(e=>{
    pno = 0;
    movePg();
  }); ////////// click /////////

  //      !
  $('.hbtn a').click(e=>{
  pno = 0;
  movePg();
  }); ////////// click /////////

  //   
  // 1. Page Up(33) / Up Arrow (38)
  // 2. Page Down(34) / Down Arrow (40)
  
  $(document).keydown((e) => {
    // 
    if (prot[0]) return;
    chkCrazy(0);

    // 
    if (e.keyCode === 33 || e.keyCode === 38) {
      pno--;
      if (pno === -1) pno = 0;
      movePg();
    }
    // 
    else if (e.keyCode === 34 || e.keyCode === 40) {
      pno++;
      if (pno === pgcnt) pno = pgcnt - 1;
      movePg();
    }
  }); ///////////// keydown ////////////////

} ///////// evtFn  ////////



//    
export { wheelFn, initSet, movePg, evtFn, zeroPno, actPage }