// TAEYANG    

//    

import React, { useRef, useState } from 'react';

// Import Swiper React components
import { Swiper, SwiperSlide } from "swiper/react";

// Import Swiper styles
import "swiper/css";
import "swiper/css/navigation"

import { Navigation } from 'swiper/modules';

//   CSS 
import "../plugin/css/swiper_ats.css"

//   
import { detailData } from "../data/detail_data.js";

//   css 
import "../plugin/css/swiper_alb.css"

// TAEYANG    
//  

import '../func/i18n';
import { useTranslation } from "react-i18next";

//  + UI
import $ from "jquery";
import "jquery-ui-dist/jquery-ui";

export function BryanChase(){
  const { t } = useTranslation();

  const selData = detailData;

  const makeList = (data) => {
    // console.log(data);
    return data.map((v, i) => (
            <div className='detail-container' key={i}>
                <div className="detail-title">
                  <h3>{t(v.ats4)}</h3>
                </div>
                <div className="detail-profile">
                  <img src={v.isrc4} alt=" " />  
                </div>
                <div className="detail-cont">
                  <div className="detail-name">
                    <h2>{t(v.alt4)}</h2>
                  </div>
                  <div className="detail-sns1">
                    <a href={v.insta4}
                      target='_blank'
                      title=" ">
                        <img src="./images/insta_img_white-remove.png" alt=" " />
                    </a>
                    <a href={v.youtube4}
                      target='_blank'
                      title=" ">
                        <img src="./images/youtube_img_white-remove.png" alt=" " />
                    </a>
                    <a href={v.twitter4}
                      target='_blank'
                      title=" ">
                      <img src="./images/twitter_img_white-remove.png" 
                      alt=" " />
                    </a>
                  </div>
                  <div className="detail-career">
                    <h2>{t("ALBUM")}</h2>
                    <p>2023 | {t("Restart It(feat. Okasian)(Extended Ver.)").split("^")[0]}</p>
                    <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{t("Restart It(feat. Okasian)(Extended Ver.)").split("^")[1]}</p>
                    <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{t("2U")}</p> 
                    <br />
                    <h2>{t("PERFORMANCE")}</h2>
                    <p>2023 | {t("2023 7    ").split("^")[0]}</p>
                    <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{t("2023 7    ").split("^")[1]}</p>
                    <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{t("RAPBEAT 2023 - ")}</p>  
                    <br />
                  </div>
                  <div className="detail-gubun">
                    <hr />
                  </div>
                  <div className="detail-gallery">
                  <h2>GALLERY</h2>
                  </div>
                    <Swiper
                      loop={true}
                      breakpoints={{
                        530:{
                          slidesPerView:3,
                          spaceBetween:30
                        },
                        300:{
                          slidesPerView:2,
                          spaceBetween:50
                        },
                      }}
                      navigation={true}
                      modules={[Navigation]}
                      spaceBetween={50}
                      className='detail-swiper'>
                      <SwiperSlide className='detail-slide'>
                        <img src={v.timg41} alt="1" />
                      </SwiperSlide >
                      <SwiperSlide className='detail-slide'>
                        <img src={v.timg42} alt="2" />
                      </SwiperSlide>
                      <SwiperSlide className='detail-slide'>
                        <img src={v.timg43} alt="3" />
                      </SwiperSlide>
                      <SwiperSlide className='detail-slide'>
                        <img src={v.timg44} alt="4" />
                      </SwiperSlide>
                      <SwiperSlide className='detail-slide'>
                        <img src={v.timg45} alt="5" />
                      </SwiperSlide>
                      <SwiperSlide className='detail-slide'>
                        <img src={v.timg46} alt="6" />
                      </SwiperSlide>
                    </Swiper>
                    <div className="detail-gubun2">
                      <hr />  
                    </div>
                    <div className="detail-video">
                      <h2>VIDEOS</h2>
                    </div>
                    <div className="detail-video-cont">
                    <div className="detail-video-grid-first">
                    <a href={v.mlink2}
                        target='_blank'
                        title=" "
                        className="video-youtube">
                        <img src={v.mv2} alt=" " />
                      </a>
                        <aside>
                        <h3>{t(v.mtit2).split("^")[0]}</h3>
                        <h3>{t(v.mtit2).split("^")[1]}</h3>
                        </aside>
                      </div>
                          <div>
                      <a href={v.mlink6}
                        target='_blank'
                        title=" "
                        className="video-youtube">
                        <img src={v.mv6} alt=" " />
                      </a>
                        <aside>
                        <h3>{t(v.mtit6)}</h3>
                        </aside>
                      </div>
                      <div>
                      <a href={v.mlink7}
                        target='_blank'
                        title=" "
                        className="video-youtube">
                        <img src={v.mv7} alt=" " />
                      </a>
                        <aside>
                        <h3>{t(v.mtit7).split("^")[0]}</h3>
                        <h3>{t(v.mtit7).split("^")[1]}</h3>
                        </aside>
                      </div>
                    </div>      
                  </div>   
              </div>   
    ))
    } /////////// makeList  ///////////

  //  ////////////////  
  return(
      <section id="ats-Detail-area4">
        {makeList(selData)}
      </section>
  );
} //////////// BryanChase  //////////////

export default BryanChase;