// TAEYANG    

//    

import React, { useRef, useState } from 'react';

// Import Swiper React components
import { Swiper, SwiperSlide } from "swiper/react";

// Import Swiper styles
import "swiper/css";
import "swiper/css/navigation"

import { Navigation } from 'swiper/modules';

//   CSS 
import "../plugin/css/swiper_ats.css"

//   
import { detailData } from "../data/detail_data.js";

//   css 
import "../plugin/css/swiper_alb.css"

//  

import '../func/i18n';
import i18next from "../func/i18n"
import { useTranslation } from "react-i18next";

//  + UI
import $ from "jquery";
import "jquery-ui-dist/jquery-ui";

export function PARKBOGUM(){
  const { t } = useTranslation();

  const selData = detailData;

  const makeList = (data) => {
    // console.log(data);
    return data.map((v, i) => (
            <div className='detail-container' key={i}>
                <div className="detail-title">
                  <h3>{t(v.ats5)}</h3>
                </div>
                <div className="detail-profile">
                  <img src={v.isrc5} alt=" " />  
                </div>
                <div className="detail-cont">
                  <div className="detail-name">
                    <h2>{t(v.alt5)}</h2>
                  </div>
                  <div className="detail-sns1">
                    <a href={v.insta5}
                      target='_blank'
                      title=" ">
                        <img src="./images/insta_img_white-remove.png" alt=" " />
                    </a>
                    <a href={v.youtube5}
                      target='_blank'
                      title=" ">
                        <img src="./images/youtube_img_white-remove.png" alt=" " />
                    </a>
                    <a href={v.twitter5}
                      target='_blank'
                      title=" ">
                      <img src="./images/twitter_img_white-remove.png" 
                      alt=" " />
                    </a>
                  </div>
                  <div className="detail-career">
                    <h2>{t("DRAMA")}</h2>
                    <p>{t("2024 | Nexflix  ")}</p>
                    <p>{t("2020 | tvN ")}</p>
                    <p>{t("2018 | tvN ")}</p>
                    <br />
                    <h2>{t("MOVIE")}</h2>
                    <p>{t("2021 | ")}</p>
                    <p>{t("2020 | ")}</p>
                    <br />
                    <h2>{t("ADVERTISING")}</h2>
                    <p>{t("2023 | ")}</p>
                    <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{t("")}</p> 
                    <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{t("")}</p> 
                  </div>
                  <div className="detail-gubun">
                    <hr />
                  </div>
                  <div className="detail-gallery">
                  <h2>GALLERY</h2>
                  </div>
                    <Swiper
                      loop={true}
                      breakpoints={{
                        530:{
                          slidesPerView:3,
                          spaceBetween:30
                        },
                        300:{
                          slidesPerView:2,
                          spaceBetween:50
                        },
                      }}
                      navigation={true}
                      modules={[Navigation]}
                      spaceBetween={50}
                      className='detail-swiper'>
                      <SwiperSlide className='detail-slide'>
                        <img src={v.timg51} alt="1" />
                      </SwiperSlide >
                      <SwiperSlide className='detail-slide'>
                        <img src={v.timg52} alt="2" />
                      </SwiperSlide>
                      <SwiperSlide className='detail-slide'>
                        <img src={v.timg53} alt="3" />
                      </SwiperSlide>
                      <SwiperSlide className='detail-slide'>
                        <img src={v.timg54} alt="4" />
                      </SwiperSlide>
                      <SwiperSlide className='detail-slide'>
                        <img src={v.timg55} alt="5" />
                      </SwiperSlide>
                      <SwiperSlide className='detail-slide'>
                        <img src={v.timg56} alt="6" />
                      </SwiperSlide>
                    </Swiper>
                    <div className="detail-gubun2">
                      <hr />  
                    </div>
                    <div className="detail-video">
                      <h2>VIDEOS</h2>
                    </div>
                    <div className="detail-video-cont">
                    <div className="detail-video-grid-first">
                      <a href={v.mlink21}
                        target='_blank'
                        title=" "
                        className="video-youtube">
                        <img src={v.mv21} alt=" " />
                      </a>
                        <aside>
                        <h3>{t(v.mtit21).split("^")[0]}</h3>
                        <h3>{t(v.mtit21).split("^")[1]}</h3>
                        </aside>
                      </div>
                          <div>
                      <a href={v.mlink22}
                        target='_blank'
                        title=" "
                        className="video-youtube">
                        <img src={v.mv22} alt=" " />
                      </a>
                        <aside>
                        <h3>{t(v.mtit22)}</h3>
                        </aside>
                      </div>
                      <div>
                      <a href={v.mlink23}
                        target='_blank'
                        title=" "
                        className="video-youtube">
                        <img src={v.mv23} alt=" " />
                      </a>
                        <aside>
                        <h3>{t(v.mtit23).split("^")[0]}</h3>
                        <h3>{t(v.mtit23).split("^")[1]}</h3>
                        </aside>
                      </div>
                      <div>
                      <a href={v.mlink24}
                        target='_blank'
                        title=" "
                        className="video-youtube">
                        <img src={v.mv24} alt=" " />
                      </a>
                        <aside>
                        <h3>{t(v.mtit24).split("^")[0]}</h3>
                        <h3>{t(v.mtit24).split("^")[1]}</h3>
                        <h3>{t(v.mtit24).split("^")[2]}</h3>
                        </aside>
                      </div>
                    </div>      
                </div>   
          </div>
    ))
    } /////////// makeList  ///////////

  //  ////////////////  
  return(
      <section id="ats-Detail-area5">
        {makeList(selData)}
      </section>
  );
} //////////// PARKBOGUM  //////////////

export default PARKBOGUM;