// TAEYANG    

//    

import React, { useRef, useState } from 'react';

// Import Swiper React components
import { Swiper, SwiperSlide } from "swiper/react";

// Import Swiper styles
import "swiper/css";
import "swiper/css/navigation"

import { Navigation } from 'swiper/modules';

//   CSS 
import "../plugin/css/swiper_ats.css"

//   
import { detailData } from "../data/detail_data.js";

//   css 
import "../plugin/css/swiper_alb.css"

//  

import '../func/i18n';
import i18next from "../func/i18n"
import { useTranslation } from "react-i18next";

//  + UI
import $ from "jquery";
import "jquery-ui-dist/jquery-ui";

export function ZionT(){
  const { t } = useTranslation();

  const selData = detailData;

  const makeList = (data) => {
    // console.log(data);
    return data.map((v, i) => (
            <div className='detail-container' key={i}>
                <div className="detail-title">
                  <h3>{t(v.ats3)}</h3>
                </div>
                <div className="detail-profile">
                  <img src={v.isrc3} alt=" " />  
                </div>
                <div className="detail-cont">
                  <div className="detail-name">
                    <h2>{t(v.alt3)}</h2>
                  </div>
                  <div className="detail-sns1">
                    <a href={v.insta3}
                      target='_blank'
                      title=" ">
                        <img src="./images/insta_img_white-remove.png" alt=" " />
                    </a>
                    <a href={v.youtube3}
                      target='_blank'
                      title=" ">
                        <img src="./images/youtube_img_white-remove.png" alt=" " />
                    </a>
                    <a href={v.twitter3}
                      target='_blank'
                      title=" ">
                      <img src="./images/twitter_img_white-remove.png" 
                      alt=" " />
                    </a>
                  </div>
                  <div className="detail-career">
                    <h2>{t("ALBUM")}</h2>
                    <p>{t("2023 | Zip")}</p>
                    <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{t("WORLD STOP TURNING (Feat. Warren Hue)").split("^")[0]}</p> 
                    <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{t("WORLD STOP TURNING (Feat. Warren Hue)").split("^")[1]}</p> 
                    <p>{t("2021 |  ")}</p>
                    <br />
                    <h2>{t("PERFORMANCE")}</h2>
                    <p>{t("2024 | 2024  -  - ").split("^")[0]}</p>
                    <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{t("2024 | 2024  -  - ").split("^")[1]}</p>
                    <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{t(" ' '").split("^")[0]}</p> 
                    <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{t(" ' '").split("^")[1]}</p> 
                    <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{t(" - ").split("^")[0]}</p> 
                    <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{t(" - ").split("^")[1]}</p> 
                    <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{t("RAPBEAT 2023 - ")}</p> 
                    <p>2023 |   {t("   - ").split("^")[0]}</p>
                    <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{t("   - ").split("^")[1]}</p>
                    <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{t("  - ").split("^")[0]}</p>  
                    <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{t("  - ").split("^")[1]}</p>  
                    <br />
                  </div>
                  <div className="detail-gubun">
                    <hr />
                  </div>
                  <div className="detail-gallery">
                  <h2>GALLERY</h2>
                  </div>
                    <Swiper
                      loop={true}
                      breakpoints={{
                        530:{
                          slidesPerView:3,
                          spaceBetween:30
                        },
                        300:{
                          slidesPerView:2,
                          spaceBetween:50
                        },
                      }}
                      navigation={true}
                      modules={[Navigation]}
                      spaceBetween={50}
                      className='detail-swiper'>
                      <SwiperSlide className='detail-slide'>
                        <img src={v.timg31} alt="1" />
                      </SwiperSlide >
                      <SwiperSlide className='detail-slide'>
                        <img src={v.timg32} alt="2" />
                      </SwiperSlide>
                      <SwiperSlide className='detail-slide'>
                        <img src={v.timg33} alt="3" />
                      </SwiperSlide>
                      <SwiperSlide className='detail-slide'>
                        <img src={v.timg34} alt="4" />
                      </SwiperSlide>
                      <SwiperSlide className='detail-slide'>
                        <img src={v.timg35} alt="5" />
                      </SwiperSlide>
                      <SwiperSlide className='detail-slide'>
                        <img src={v.timg36} alt="6" />
                      </SwiperSlide>
                    </Swiper>
                    <div className="detail-gubun2">
                      <hr />  
                    </div>
                    <div className="detail-video">
                      <h2>VIDEOS</h2>
                    </div>
                    <div className="detail-video-cont">
                    <div className="detail-video-grid-first">
                    <a href={v.mlink1}
                        target='_blank'
                        title=" "
                        className="video-youtube">
                        <img src={v.mv1} alt=" " />
                      </a>
                        <aside>
                        <h3>{t(v.mtit1).split("^")[0]}</h3>
                        <h3>{t(v.mtit1).split("^")[1]}</h3>
                        </aside>
                      </div>
                          <div>
                      <a href={v.mlink8}
                        target='_blank'
                        title=" "
                        className="video-youtube">
                        <img src={v.mv8} alt=" " />
                      </a>
                        <aside>
                        <h3>{t(v.mtit8)}</h3>
                        </aside>
                      </div>
                      <div>
                      <a href={v.mlink14}
                        target='_blank'
                        title=" "
                        className="video-youtube">
                        <img src={v.mv14} alt=" " />
                      </a>
                        <aside>
                        <h3>{t(v.mtit14)}</h3>
                        </aside>
                      </div>
                      <div>
                      <a href={v.mlink19}
                          target='_blank'
                          title=" " 
                          className="video-youtube">
                        <img src={v.mv19} alt=" " />
                      </a>
                        <aside>
                        <h3>{t(v.mtit19).split("^")[0]}</h3>
                        <h3>{t(v.mtit19).split("^")[1]}</h3>
                        </aside>
                      </div>
                      <div>
                      <a href={v.mlink20}
                          target='_blank'
                          title=" " 
                          className="video-youtube">
                        <img src={v.mv20} alt=" " />
                      </a>
                        <aside>
                        <h3>{t(v.mtit20).split("^")[0]}</h3>
                        <h3>{t(v.mtit20).split("^")[1]}</h3>
                        </aside>
                      </div>
                    </div>      
                  </div>   
              </div>   
    ))
    } /////////// makeList  ///////////

  //  ////////////////  
  return(
      <section id="ats-Detail-area3">
        {makeList(selData)}
      </section>
  );
} //////////// ZionT  //////////////

export default ZionT;