//    

import React, { useEffect, useRef, useState } from 'react';

// Import Swiper React components
import { Swiper, SwiperSlide } from "swiper/react";

// Import Swiper styles
import "swiper/css";
import 'swiper/css/effect-cards';
import 'swiper/css/pagination';
import 'swiper/css/navigation';

//  
// import "../func/cardEffect.js";

//  CSS 
import "../plugin/css/swiper_ats.css";

//   
import { atsData } from "../data/ats_data";

//   
import { EffectCards, Pagination, Navigation, EffectFlip } from "swiper/modules";

//  + UI
import $ from "jquery";
import "jquery-ui-dist/jquery-ui";
import { Link } from 'react-router-dom';

//  

import '../func/i18n';
import { useTranslation } from "react-i18next";

/////  ////////////////
export function Swiper_ats(){
const { t } = useTranslation();
const [activeCard, setActiveCard] = useState(false);

const handleFilp = () => {
  setActiveCard(!activeCard)
}

const swRef = useRef(null);

const selData = atsData;

  //    ////////////
  useEffect(()=>{
    
      // Read More     
    $('.ats_Rbtn').click(e=>{
      $(e.currentTarget).parents('.cont-bx').addClass('on');
      
      // Read More     
      $('.ats_Rbtn').fadeOut(400);
      $('.ats_Cbtn').fadeIn(400);
      
      swRef.current.swiper.disable();

  }); /////////// click ////////////

    // Close     
    $('.ats_Cbtn').click(e=>{
      $(e.currentTarget).parents('.cont-bx').removeClass('on');
  
      // Close     
      $('.ats_Rbtn').fadeIn(400);
      $('.ats_Cbtn').fadeOut(400);
      
      swRef.current.swiper.enable();
      
    }); /////////// click ////////////

    // Close     
    $('.xbtn').click(e=>{
      $(e.currentTarget).parents('.cont-bx').removeClass('on');
  
      // Close     
      $('.ats_Rbtn').fadeIn(400);
      $('.ats_Cbtn').fadeOut(400);
      
      swRef.current.swiper.enable();
      
    }); /////////// click ////////////

    // Close     
    $('.ats_Fbtn').click(e=>{
  
      // Close     
      $('.ats_Rbtn').fadeOut(400);
      
      swRef.current.swiper.disable();
      
    }); /////////// click ////////////

    $('.xbtn1').click(e=>{
      
      swRef.current.swiper.enable();
      
    }); /////////// click ////////////
      
  },[]); ///////// useEffect /////////
   

//   ///////////
const makeList = (data) => {
  // console.log(data);

  return data.map((v,i) => (
    <SwiperSlide key={i}>
      {/*   */}
        <div 
        className={`cont-bx ${activeCard? 'cardFlip':''}`}>
          <div className="intro-bx">
            <img
            src={v.isrc}
            alt={v.ats}
            id="profile"/>
            {/*   */}
            <div className="ptit">
              <h2>{t(v.itit)}</h2>
            </div>
              <button 
              onClick={handleFilp}
              className={"ats_Fbtn"}>
                Flip
              </button>
              <button className={"ats_Rbtn"}>
                Read More
              </button>
              <button className="ats_Cbtn">
                Close
              </button>
          </div>
          <div className="desc-bx">
            <h2>{t("ABOUT")}</h2>
            <h3>{t(v.dcont)}</h3>
            <div
            className="xbtn">
              <img src="./images/xmark-solid.png" alt="x" />
            </div>
            <div
            onClick={handleFilp}  
            className="xbtn1">
              <img src="./images/xmark-solid.png" alt="x" />
            </div>
            <div className="sns">
              <a href={v.insta}
              target='_blank'
              title=" "
              className="instagram">
                <img src="./images/instagram.png" alt=" " />
              </a>
              <a href={v.youtube}
              target='_blank'
              title=" "
              className="youtube">
                <img src="./images/youtube.png" alt=" " />
              </a>
              <a href={v.twitter}
              target='_blank'
              title=" "
              className="twitter">
              <img src="./images/twitter.png"
              alt=" " />
              </a>
            </div>
            <button className="ats-more1">
              <Link to={v.link}>Detail More</Link>
            </button>
              <Swiper
              loop={true}
              pagination={{
                type:'fraction',
              }}
              breakpoints={{
                1380: {
                  slidesPerView:3,
                  spaceBetween:10
                },
                1379: {
                  spaceBetween:1,
                  slidesPerView:2
                }
              }}
              navigation={true}
              modules={[Pagination, Navigation]}
              className='img_swiper'>
                <SwiperSlide>
                  <a href={v.cont1}
                  target='_blank'
                  title=' '
                  className='ats_cont'>
                    <img src={v.timg1} alt="1" />
                  </a>
                </SwiperSlide>
                <SwiperSlide>
                  <a href={v.cont2}
                  target='_blank'
                  title=' '
                  className='ats_cont'>
                    <img src={v.timg2} alt="1" />
                  </a>
                </SwiperSlide>
                <SwiperSlide>
                  <a href={v.cont3}
                  target='_blank'
                  title=' '
                  className='ats_cont'>
                    <img src={v.timg3} alt="1" />
                  </a>
                </SwiperSlide>
                <SwiperSlide>
                  <a href={v.cont4}
                  target='_blank'
                  title=' '
                  className='ats_cont'>
                    <img src={v.timg4} alt="1" />
                  </a>
                </SwiperSlide>
              </Swiper>
            </div>
          </div>
    </SwiperSlide>
  ));
}; ////////// makeList  //////

//  /////////////
  return(
    <>
      <div className="ats-title">
        <h3>ARTISTS</h3>
      </div>
      <Swiper 
      ref={swRef}
      effect='cards'
      modules={[EffectCards, EffectFlip]}
      grabCursor={true}
      loop={true}
      cardsEffect={{
        rotate:0,
        depth: 100,
        modifier: 1,
        slideShadows:false,
        loopAdditionalSlides : 1,
      }}
      // onClick={(swiper) => {
      //   swiper.allowTouchMove = false;
      //   swiper.unsetGrabCursor();
      // }}
      // onSlideChange={(swiper) => {
      //   swiper.allowTouchMove = true;
      // }}
      //   
      className="ats_swiper">
      {makeList(selData)}
      </Swiper>
    </>
  );
} ////////// SwiperVid  /////////////