// TAEYANG    

//    

import React, { useRef, useState } from 'react';

// Import Swiper React components
import { Swiper, SwiperSlide } from "swiper/react";

// Import Swiper styles
import "swiper/css";
import "swiper/css/navigation"

import { Navigation } from 'swiper/modules';

//   CSS 
import "../plugin/css/swiper_ats.css"

//   
import { detailData } from "../data/detail_data.js";

//   css 
import "../plugin/css/swiper_alb.css"

//  

import '../func/i18n';
import i18next from "../func/i18n"
import { useTranslation } from "react-i18next";

//  + UI
import $ from "jquery";
import "jquery-ui-dist/jquery-ui";

export function LEEJONGWON(){
  const { t } = useTranslation();

  const selData = detailData;

  const makeList = (data) => {
    // console.log(data);
    return data.map((v, i) => (
            <div className='detail-container' key={i}>
                <div className="detail-title">
                  <h3>{t(v.ats6)}</h3>
                </div>
                <div className="detail-profile">
                  <img src={v.isrc6} alt=" " />  
                </div>
                <div className="detail-cont">
                  <div className="detail-name">
                    <h2>{t(v.alt6)}</h2>
                  </div>
                  <div className="detail-sns1">
                    <a href={v.insta6}
                      target='_blank'
                      title=" ">
                        <img src="./images/insta_img_white-remove.png" alt=" " />
                    </a>
                    <a href={v.youtube6}
                      target='_blank'
                      title=" ">
                        <img src="./images/youtube_img_white-remove.png" alt=" " />
                    </a>
                    <a href={v.twitter6}
                      target='_blank'
                      title=" ">
                      <img src="./images/twitter_img_white-remove.png" 
                      alt=" " />
                    </a>
                  </div>
                  <div className="detail-career">
                    <h2>{t("DRAMA")}</h2>
                    <p>{t(" | ()   ")}</p>
                    <p>"2024 | {t("MBC   ")}</p>
                    <p>"2022 | {t("MBC ")}</p>
                    <br />
                    <h2>{t("MOVIE")}</h2>
                    <p>"2020 | {t("")}</p>
                    <p>"2019 | {t(" ")}</p>
                    <p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{t("")}</p> 
                    <br />
                  </div>
                  <div className="detail-gubun">
                    <hr />
                  </div>
                  <div className="detail-gallery">
                  <h2>GALLERY</h2>
                  </div>
                    <Swiper
                      loop={true}
                      breakpoints={{
                        530:{
                          slidesPerView:3,
                          spaceBetween:30
                        },
                        300:{
                          slidesPerView:2,
                          spaceBetween:50
                        },
                      }}
                      navigation={true}
                      modules={[Navigation]}
                      spaceBetween={50}
                      className='detail-swiper'>
                      <SwiperSlide className='detail-slide'>
                        <img src={v.timg61} alt="1" />
                      </SwiperSlide >
                      <SwiperSlide className='detail-slide'>
                        <img src={v.timg62} alt="2" />
                      </SwiperSlide>
                      <SwiperSlide className='detail-slide'>
                        <img src={v.timg63} alt="3" />
                      </SwiperSlide>
                      <SwiperSlide className='detail-slide'>
                        <img src={v.timg64} alt="4" />
                      </SwiperSlide>
                      <SwiperSlide className='detail-slide'>
                        <img src={v.timg65} alt="5" />
                      </SwiperSlide>
                      <SwiperSlide className='detail-slide'>
                        <img src={v.timg66} alt="6" />
                      </SwiperSlide>
                    </Swiper>
                    <div className="detail-gubun2">
                      <hr />  
                    </div>
                    <div className="detail-video">
                      <h2>VIDEOS</h2>
                    </div>
                    <div className="detail-video-cont">
                    <div className="detail-video-grid-first">
                      <a href={v.mlink25}
                        target='_blank'
                        title=" "
                        className="video-youtube">
                        <img src={v.mv25} alt=" " />
                      </a>
                        <aside>
                        <h3>{t(v.mtit25).split("^")[0]}</h3>
                        <h3>{t(v.mtit25).split("^")[1]}</h3>
                        </aside>
                      </div>
                          <div>
                      <a href={v.mlink26}
                        target='_blank'
                        title=" "
                        className="video-youtube">
                        <img src={v.mv26} alt=" " />
                      </a>
                        <aside>
                        <h3>{t(v.mtit26).split("^")[0]}</h3>
                        <h3>{t(v.mtit26).split("^")[1]}</h3>
                        </aside>
                      </div>
                      <div>
                      <a href={v.mlink27}
                        target='_blank'
                        title=" "
                        className="video-youtube">
                        <img src={v.mv27} alt=" " />
                      </a>
                        <aside>
                        <h3>{t(v.mtit27).split("^")[0]}</h3>
                        <h3>{t(v.mtit27).split("^")[1]}</h3>
                        </aside>
                      </div>
                      <div>
                      <a href={v.mlink28}
                        target='_blank'
                        title=" "
                        className="video-youtube">
                        <img src={v.mv28} alt=" " />
                      </a>
                        <aside>
                        <h3>{t(v.mtit28).split("^")[0]}</h3>
                        <h3>{t(v.mtit28).split("^")[1]}</h3>
                        </aside>
                      </div>
                    </div>      
                </div>   
          </div>
    ))
    } /////////// makeList  ///////////

  //  ////////////////  
  return(
      <section id="ats-Detail-area6">
        {makeList(selData)}
      </section>
  );
} //////////// LEEJONGWON  //////////////

export default LEEJONGWON;